.icon{
    color: red;
    font-size: 30px;
}
body{
    background-color:#F9CF93;
}
.grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap:15px;
}
.box{
    width:auto;
    height:100px;
    display: flex;
    justify-content: center;
    align-items:center;
}
button{
    margin-top: 5px;
    margin-bottom: 5px;
}